  
.post {
    width: 100%;
    border-radius: 10px;
    -webkit-box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
    box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
    margin: 30px 0;
    
  }
  
  .postWrapper {
    padding: 10px;
  }
  
  .postTop {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .postTopLeft {
    display: flex;
    align-items: center;
    /* display: flex; */
    align-items: center;
    justify-content: center;
  }
  
  .postProfileImg {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
  }
  
  .postUsername {
    font-size: 15px;
    font-weight: 500;
    margin: 0 10px;
    
    
  }
  
  .postDate{
      font-size: 12px;
  }
  
  .postCenter{
      margin: 20px 0;
  }
  
  .postImg{
      margin-top: 20px;
      width: 100%;
      max-height: 500px;
      object-fit: contain;
      /* z-index: -1; */
  }
  
  .postBottom{
      display: flex;
      align-items: center;
      justify-content: space-between;
  }
  
  .postBottomLeft{
      display: flex;
      align-items: center;   
  }
  
  .likeIcon{
      width: 24px;
      height: 24px;
      margin-right: 5px;
      cursor: pointer;
  }
  
  .postLikeCounter{
      font-size: 15px;
  }
  
  .postCommentText{
      cursor: pointer;
      border-bottom: 1px dashed gray;
      font-size: 15px;
  }
  .postComment{
    flex:12;
    /* align-items: center; */
    /* justify-content: space-between; */
}
.postCommentInput {
  margin: 5px 15px;
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  flex: 1;
}
.commentInput {
  flex: 1;
  outline: none;
  border: none;
}
.commentButton{
  background-color: transparent;
  color: #0f9bf7;
  outline: none;
  border: none;
  pointer-events: cursor;
}
.postComments {
  display: flex;
  flex-direction: column;
  /* margin: 0px 15px; */
 
  
}
.postCommentsMoreCount {
  font-size: 16px;
  color: #8e8e8e;
  margin-bottom: 10px;
}
.postComment {
  display: flex;
  flex-direction: row;
  flex: 1;
 
  /* span {
    color: #262626;
    &:nth-child(1) {
      font-weight: 600;
    }
  } */
  /* svg {
    height: 12px;
    width: 12px;
    margin-left: auto;
  } */
}
.commentSection {
  margin-top: 20px;
}
.postDate {
  margin: 0px 15px;
  
}
.commentTime {
  color: #8e8e8e;
  font-size: 10px;
}


.comment-main-div {
  color: rgb(7, 7, 7);
  padding: 1px;
  border: 1px solid rgb(206, 206, 206);
  margin: 5px;
  width: 100%;
}
.comment-main-div img {
  border-radius: 50%;
  width: 23px;
  height: 21px;
  margin-top: 3px;
}
.replay-text {
  color: #02afff;
  margin-left: 83px;
  font-size: 12px;

}
.reply-div {
  margin: 2px 0 0 40px;
  display: none;
}
.postText{
  /* margin-bottom: 5px; */
  font-size: 13px;
}
.postDescription{
  padding-bottom:50px
}

/* POPUP CSS */



.channel-popup-main-div5 {
    position: fixed;
    top: 80px;
    left: 350px;
    width: 50%;
    height: 40vh;
    /* background-color: rgba(231, 231, 231, 0.644); */
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .channel-popup-main-div5 .channel-popup-inner-div5 {
    position: relative;
    padding: 12px;
    width: 1030px;
    line-height: 1.6;
    height: 40vh;
    /* background: rgb(255, 255, 255); */
    /* box-shadow: 2px 1px 2px 1px #aaaaaa; */
  }
  .close-btn {
  float: right;
  width: 29px;
  height: 21px;
  background-color: red;
  border: 1px solid rgb(197, 197, 197);
  color: rgb(114, 114, 114);
  }
  .close-btn:hover {
  background-color: rgb(238, 238, 238);
  border: 1px solid rgb(197, 197, 197);
  color: rgb(90, 90, 90);
  }
  .close-btn:focus {
  background: rgb(231, 227, 228);
  }
  .channel-popup-inner-div .popup-icon {
  padding: 5px;
  }
  
  .channel-popup-inner-div .popup-text {
  padding: 0px 0px 0px 9px;
  font-size: 23px;
  color: rgb(88, 88, 88);
  }

   /* POPUPFORM CSS */
   .user-registration-wrapper5{
    height: 40vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--color-light);
}
.user-registration-wrapper5 .user-registration-sub-wrapper{
    height: 40vh;
    margin-top: 100px;
    width: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-shadow: var(--shadow-large);
    transition: 0.3s ease-in;
    background-color: var(--light-col);
}
.user-registration-wrapper5 .user-registration-sub-wrapper:hover{
    transform:scale(1.04);
}
.user-registration.heading-wrapper{
    font-weight: 350;
    display: flex;
    justify-content: left;
    /* margin-top: 2rem; */
    margin-bottom: 1rem;
}
.user-registration-field-wrapper{
    width: 100%;
    background-color: var(--light-col);
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
    margin-top: 10px;
}
.field-wrapper h4{
    font-size: 1rem;
    font-weight: 300;
    /* margin-bottom: 2rem; */
}
.user-registration-wrapper5 .button-wrapper{
    height: 40px;
    width: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.3s ease-in;
    background-color: var(--dark-col);
    margin: 1rem 0;
    cursor: pointer;
}
.user-registration-wrapper5 .button-wrapper:hover{
    background-color: var(--light-btn);
}
.user-registration-wrapper5 .button-wrapper a{
    color: var(--color-light);
    text-decoration: none;
}
